// –∫—É–¥–∞ –ø–∏—à–µ—Ç—Å—è —Ç–µ–∫—Å—Ç —Å–æ–æ–±—â–µ–Ω–∏—è
let input=document.querySelector('.form-input');
// –∫–Ω–æ–ø–∫–∏ –æ—Ç–ø—Ä–∞–≤–∏—Ç—å
let submit=document.querySelector('.form-submit');

submit.addEventListener('click',(event)=>{
    if (input.value===''){
        input.value='–í—ã –Ω–∏—á–µ–≥–æ –Ω–µ –≤–≤–µ–ª–∏'
    }
    else {
        let message=input.value
        input.value=''

        let newDate=new Date();
        let messageObject={
            date:''+(newDate.getHours())+':'+(newDate.getMinutes()),
            messages:message
        }
        input.value="–í " + messageObject.date + " –ø—ã—Ç–∞–ª–∏—Å—å –Ω–∞–π—Ç–∏: " + messageObject.messages
    }
})